import React, { Component } from 'react';
// import Calculator from "./Functional.js";
import './App.css';

class App extends Component {
  state={
    firstNumber:0,
    lastOperation: "AC",
    total: 0,
    arrayOfButtons: [],
    showScientific:false,
    showScientificText:"Show Scientific",
    valueInMemory:0,
    angleUnit:"Degree"
  }

  evaluate = () => {
    switch (this.state.lastOperation) {
      case "Plus":
        this.setState({total:Number(this.state.firstNumber)+Number(this.state.total)});
        this.setState({firstNumber:Number(this.state.firstNumber)+Number(this.state.total)});
        break;
      case "Minus":
        this.setState({total:Number(this.state.firstNumber)-Number(this.state.total)});
        this.setState({firstNumber:Number(this.state.firstNumber)-Number(this.state.total)});
        break;
      case "Times":
        this.setState({total:Number(this.state.firstNumber)*Number(this.state.total)});
        this.setState({firstNumber:Number(this.state.firstNumber)*Number(this.state.total)});
        break;
      case "Divide":
        this.setState({total:Number(this.state.firstNumber)/Number(this.state.total)});
        this.setState({firstNumber:Number(this.state.firstNumber)/Number(this.state.total)});
        break;
      default:
    }
  }

  clickButton = (event) => {
    let buttonClicked;
    if(event.target.id==="") {
      buttonClicked=event.target.parentElement
    }
    else {
      buttonClicked=event.target
    }
    console.log(buttonClicked.id);
    console.log(this.state.arrayOfButtons);
    switch (buttonClicked.id) {
      case "AC":
        this.setState({firstNumber:0});
        this.setState({total:0});
        this.setState({lastOperation:"AC"});
        this.setState({arrayOfButtons:[]})
        break;
      case "PlusMinus":
        this.setState({total:-1*this.state.total});
        this.evaluate();
        break;
      case "Percent":
        this.setState({total:this.state.total/100});
        break;
      case "SquareRoot":
        this.setState({total:Math.sqrt(this.state.total)});
        break;
      case "Point":
        this.setState({total:this.state.total+"."});
        break;
      case "Divide":
        if (this.state.lastOperation!=="AC"&&this.state.lastOperation!=="Equals") {
          this.evaluate()
        }
        else {this.setState({firstNumber:Number(this.state.total)})}
        this.setState({lastOperation:"Divide"});
        this.setState({total:""});
        break;
      case "Times":
        if (this.state.lastOperation!=="AC"&&this.state.lastOperation!=="Equals") {
          this.evaluate()
        }
        else {this.setState({firstNumber:Number(this.state.total)})}
        this.setState({lastOperation:"Times"});
        this.setState({total:""});
        break;
      case "Minus":
        if (this.state.lastOperation!=="AC"&&this.state.lastOperation!=="Equals") {
          this.evaluate()
        }
        else {this.setState({firstNumber:Number(this.state.total)})}
        this.setState({lastOperation:"Minus"});
        this.setState({total:""});
        break;
      case "Plus":
        if (this.state.lastOperation!=="AC"&&this.state.lastOperation!=="Equals") {
          this.evaluate()
        }
        else {this.setState({firstNumber:Number(this.state.total)})}
        this.setState({lastOperation:"Plus"});
        this.setState({total:""});
        break;
      case "Equals":
        this.evaluate();
        this.setState({lastOperation:"Equals"});
        break;
      case "ToggleScientific":
        this.setState({showScientific:!this.state.showScientific});
        if(this.state.showScientific) {this.setState({showScientificText:"Show Scientific"})}
        else {this.setState({showScientificText:"Hide Scientific"})}
        break;
      case "MClear":
        this.setState({valueInMemory:0});
        break;
      case "MStore":
        this.setState({valueInMemory:this.state.display});
        break;
      case "MRecall":
        this.setState({display:this.state.valueInMemory});
        break;
      case "RadiansDegrees":
        if (this.state.angleUnit==="Degree") {
          this.setState({angleUnit:"Radian"})
        }
        else {this.setState({angleUnit:"Degree"})}
        break;
      default:
    }
    if (buttonClicked.id.substr(0,6)==="Button") {
      if (this.state.total===0|isNaN(this.state.total)) {
        this.setState({total:buttonClicked.id.substr(-1)})
      }
      else {
        this.setState({total:this.state.total.toString()+buttonClicked.id.substr(-1)})
      }
      this.state.arrayOfButtons.push(buttonClicked.id.substr(-1))
    }
    else {
      this.state.arrayOfButtons.push(buttonClicked.id)
    }
  }
  
  render() {
    return (
      <div className="App">
        <div>
          <h1>Duncan&rsquo;s calculator</h1> 
          {/* <Button id="ToggleScientific" innerHTML={this.state.showScientificText} clickButton={this.clickButton}/> */}
        </div>
        { this.state.showScientific ? <ScientificCalculator total={this.state.total} clickButton={this.clickButton}/> : <Calculator total={this.state.total} clickButton={this.clickButton}/> }
      </div>
    );
  }
}

const ScientificCalculator = (props) => {
  return (
    <div className="scientificCalculator">
      <ScientificDisplay total={props.total}/>
      <div className="bothButtonSets">
        <ScientificButtons clickButton={props.clickButton}/>
        <Buttons clickButton={props.clickButton}/>
      </div>
    </div>
  )
}

const Calculator = (props) => {
  return (
    <div className="calculator">
      <Display total={props.total}/>
      <Buttons clickButton={props.clickButton}/>
    </div>
  )
}

const ScientificDisplay = (props) => {
  return (
    <div className="scientificDisplay">
      <div>
        {props.total}
      </div>
    </div>
  )
}

const Display = (props) => {
  return (
      <div className="display">
          <div>
              {props.total}
          </div>
      </div>
  )
}

const ScientificButtons = (props) => {
  return (
    <div className="scientificButtons">
          <Button id="MClear" innerHTML="MC" clickButton={props.clickButton}/>
          <Button id="MStore" innerHTML="MS" clickButton={props.clickButton}/>
          <Button id="MRecall" innerHTML="MR" clickButton={props.clickButton}/>
          <Button id="RadiansDegrees" innerHTML="rad/&deg;" clickButton={props.clickButton}/>
          <Button id="YthRootOfX" innerHTML=" &#696;&radic;x" clickButton={props.clickButton}/>
          <Button id="XToTheY" innerHTML="x&#696;" clickButton={props.clickButton}/>
          <Button id="YToTheX" innerHTML="y&#739;" clickButton={props.clickButton}/>
          <Button id="Phi" innerHTML="&phi;" clickButton={props.clickButton}/>
          <Button id="LogXBaseY" innerHTML="logᵧx" clickButton={props.clickButton}/>
          <Button id="Ln" innerHTML="ln" clickButton={props.clickButton}/>
          <Button id="EToTheX" innerHTML="e&#739;" clickButton={props.clickButton}/>
          <Button id="E" innerHTML="e" clickButton={props.clickButton}/>
          <Button id="Sin" innerHTML="sin" clickButton={props.clickButton}/>
          <Button id="Cos" innerHTML="cos" clickButton={props.clickButton}/>
          <Button id="Tan" innerHTML="tan" clickButton={props.clickButton}/>
          <Button id="Pi" innerHTML="&pi;" clickButton={props.clickButton}/>
          <Button id="OneOverX" innerHTML="1/x" clickButton={props.clickButton}/>
          <Button id="LeftBracket" innerHTML="(" clickButton={props.clickButton}/>
          <Button id="RightBracket" innerHTML=")" clickButton={props.clickButton}/>
          <Button id="Rand" innerHTML="rand" clickButton={props.clickButton}/>
    </div>
  )
}

const Buttons = (props) => {
  return (
      <div className="buttons">
          <Button id="AC" innerHTML="AC" clickButton={props.clickButton}/>
          <Button id="PlusMinus" innerHTML="&plusmn;" clickButton={props.clickButton}/>
          <Button id="Percent" innerHTML="%" clickButton={props.clickButton}/>
          <Button id="Divide" innerHTML="&divide;" clickButton={props.clickButton}/>
          <Button id="Button7" innerHTML="7" clickButton={props.clickButton}/>
          <Button id="Button8" innerHTML="8" clickButton={props.clickButton}/>
          <Button id="Button9" innerHTML="9" clickButton={props.clickButton}/>
          <Button id="Times" innerHTML="&times;" clickButton={props.clickButton}/>
          <Button id="Button4" innerHTML="4" clickButton={props.clickButton}/>
          <Button id="Button5" innerHTML="5" clickButton={props.clickButton}/>
          <Button id="Button6" innerHTML="6" clickButton={props.clickButton}/>
          <Button id="Minus" innerHTML="-" clickButton={props.clickButton}/>
          <Button id="Button1" innerHTML="1" clickButton={props.clickButton}/>
          <Button id="Button2" innerHTML="2" clickButton={props.clickButton}/>
          <Button id="Button3" innerHTML="3" clickButton={props.clickButton}/>
          <Button id="Plus" innerHTML="+" clickButton={props.clickButton}/>
          <Button id="Button0" innerHTML="0" clickButton={props.clickButton}/>
          <Button id="Point" innerHTML="." clickButton={props.clickButton}/>
          <Button id="SquareRoot" innerHTML="&radic;" clickButton={props.clickButton}/>
          <Button id="Equals" innerHTML="=" clickButton={props.clickButton}/>
      </div>
  )
}

const Button = (props) => {
  return (
      <div className="button" id={props.id} onClick={props.clickButton}>
      <div>
          {props.innerHTML}
      </div>
      </div>
  )
}

export default App;